@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 60px;
  width: 100%;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.05);
}

.navigation_list {
  display: flex;
  width: 200px;
  margin: auto;
  justify-content: space-between;
  margin-right: 100px;
}

.navigation_list li {
  list-style-type: none;
}

.navigation_list a {
  text-transform: uppercase;
  text-decoration: none;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.navigation_list a:hover {
  color: #00ACDC;
}

.activeLink {
  color: #00ACDC;
}

.navigation_cart {
  position: relative;
  display: block;
  background-image: url(data:image/png;base64,);
  width: 32px;
  height: 32px;
}

.navigation_cart:hover {
  background-image: url(data:image/png;base64,);
}
.home {
  width: 300px;
  position: absolute;
  left: calc(50% - 150px);
  top: 50%;
}

.home p {
  text-align: center;
}
.product {
  width: 200px;
  padding: 10px;
}

.product_title {
  text-align: center;
}

.product_description {
  font-size: 13px;
}

.product_price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.product_price > span {
  font-size: 22px;
  font-weight: bold;
}

.product_buttonBuy {
  height: 40px;
  outline: none;
  width: 100%;
  border: none;
  background-color: #00ACDC;
  border-radius: 5px;
  cursor: pointer;
  color: #fff;
  text-transform: uppercase;
  font-size: 16px;
  font-weight: bold;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.product_buttonBuy:hover {
  color: #00ACDC;
  background: #fff;
  border: 1px solid #00ACDC;
}

.cartProduct {
  border-bottom: 2px solid #e6ecf1;
}

.cartProduct_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  max-width: 1200px;
  padding: 10px 0;
}

.cartProduct_buttonDelete {
  width: 30px;
  height: 30px;
  font-size: 30px;
  font-weight: 500;
  outline: none;
  border: none;
  background-color: #fff;
  align-self: center;
  cursor: pointer;
  color: #b6b9bb;
}

.cartProduct_buttonDelete:hover {
  color: #00ACDC;
}

.cartProduct_description {
  width: 60%;
}

.cartProduct_description > h1 {
  margin: 0 0 5px;
}

.cartProduct_description > p {
  margin: 0;
}

.cartProduct_button {
  width: 30px;
  height: 30px;
  font-size: 20px;
  background-color: #e6ecf1;
  outline: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.cartProduct_button:hover {
  color: #00ACDC;
}

.cartProduct_displayCount {
  display: inline-block;
  width: 40px;
  text-align: center;
}

.cartProduct_price {
  display: inline-block;
  width: 80px;
  text-align: center;
  font-size: 30px;
}

.cartProduct_button__minus:disabled,
.cartProduct_button__minus:disabled:hover {
  color: rgb(105, 99, 99);
}

@media (max-width: 535px) {
  .cartProduct_wrapper {
    flex-direction: column;
  }

  .cartProduct_description {
    margin: 20px 0;
  }

  .cartProduct_description h1 {
    text-align: center;
  }

  .cartProduct_price {
    margin-top: 20px;
  }
}
.catalog {
  display: flex;
  justify-content: space-between;
  padding: 50px 20px 0;
  max-width: 1200px;
  margin: auto;
  flex-wrap: wrap;
}

@media (max-width: 937px) {
  .catalog {
    justify-content: space-around;
  }
}
.cartCatalog_totalPrice {
 display: flex;
 justify-content: flex-end;
 align-items: center;
 max-width: 1200px;
 margin: auto;
}

.cartCatalog_totalPrice > p {
    display: inline-block;
    width: 80px;
    text-align: center;
    font-size: 30px;

}

.cartCatalog_totalPrice > span {
  font-size: 13px;
  margin-right: 20px;
} 

.cartCatalog_empty {
  width: 300px;
  text-align: center;
  font-size: 30px;
  position: absolute;
  left: calc(50% - 100px);
  top: 50%;
}

@media (max-width: 535px) {
  .cartCatalog_totalPrice {
    justify-content: center;
    margin-right: 45px;
  }
}
